type Props = {
  key?: number,
  title?: string,
  icon?: React.ReactNode,
  badgeValue?: string,
  badgeColor?: ColorPalette,
  badgeWrap?: boolean,
  dateStart?: string,
  dateEnd?: string,
  description?: string
}
const TimelineFactory: React.FC<Props> = (
  props, key
) => {
  const {
    title,
    icon = <LuCheck />,
    badgeValue,
    badgeColor = 'green',
    badgeWrap = false,
    dateStart,
    dateEnd,
    description
  } = props
  return (
    <Timeline.Item key={key}>
      <Timeline.Connector>
        <Timeline.Separator />
        <Timeline.Indicator>
          {icon}
        </Timeline.Indicator>
      </Timeline.Connector>
      <Timeline.Content>
        {badgeWrap && badgeValue && <Badge width='fit-content' colorPalette={badgeColor}>{badgeValue}</Badge>}
        <Timeline.Title>{!badgeWrap && badgeValue && <Badge colorPalette={badgeColor}>{badgeValue}</Badge>}{title}</Timeline.Title>
        {(dateStart || dateEnd) && <Timeline.Description>{dateStart}{dateEnd && `~ ${dateEnd}`}</Timeline.Description>}
        {description && <Text textStyle="sm">{description}</Text>}
      </Timeline.Content>
    </Timeline.Item>
  )
}

type AwardItem = {
  key?: number,
  title: string,
  date: string,
  grade: string,
  color: ColorPalette,
  count: number,
  inout: number,
  org: string
}

const AwardFactory: React.FC<AwardItem> = (
  props, key
) => {
  const {
    title, date, grade, color, count, inout, org
  } = props
  return (
    <Card.Root>
      <Card.Body>
        <Stack>
          <Stack direction='row'>
            <Badge colorPalette={color} width='fit-content'>{grade}</Badge>
            <Badge colorPalette={!inout ? 'blue' : 'green'} width='fit-content'>{!inout ? '' : ''}</Badge>
          </Stack>
          <Stack gap='0px'>
            <Text fontSize='sm' fontWeight='semibold'>{title}</Text>
            <Text fontSize='xs' color='fg.muted'>{date}</Text>
          </Stack>
          <Stack gap='3'>
            <Stack direction='row' alignItems='center'>
              <Icon size={{ base: 'xs', md: 'md' }}><LuUser/></Icon>
              <Text fontSize={{ base: 'xs', md: 'sm' }} lineHeight='1'>{count == 1 ? '' : ` (${count})`}</Text>
            </Stack>
            <Stack direction='row' alignItems='center'>
              <Icon size={{ base: 'xs', md: 'md' }}><LuBuilding/></Icon>
              <Text fontSize={{ base: 'xs', md: 'sm' }} lineHeight='1'>{org}</Text>
            </Stack>
          </Stack>
        </Stack>
      </Card.Body>
    </Card.Root>
  )
}

